import { connect }       from "react-redux";
import { AlertTriangle } from "react-feather";
import { useForm }       from "react-hook-form";

// Import Own Components
import { bindAll } from "Helpers";
import toaster     from "Components/Global/Toaster";
import AuthService from "Store/Services/AuthService";
import LoginPage   from "./LoginPage.jsx";

const LoginPageContainer = ({ authService }) => {

	const { handleSubmit, ...hookForm } = useForm({
		defaultValues : {
			user     : "",
			password : "",
		},
	});

	const handleLoginSubmit = (extraProps) => handleSubmit(async data => {
		const {
			handleStartLoading   = () => {},
			handleFinishLoading  = () => {},
		} = extraProps;

		if (typeof(handleStartLoading) === "function") {
			handleStartLoading();
		}

		const status = await authService.login({
			username : data.user,
			password : data.password,
		}).unwrap();


		if (typeof(handleStartLoading) === "function") {
			handleFinishLoading();
		}

		if (status === 200) {
			return;
		}

		if (status === 404 || status === 400) {
			hookForm.setError("user", {
				type    : "manual",
				message : "Credenciales erroneas.",
			});

			hookForm.setError("password",    {
				type    : "manual",
				message : "Credenciales erroneas.",
			});
		} else if (status === 409) {
			toaster.info({
				icon  : AlertTriangle,
				title : "Cuenta Inactiva",
				body  : "Tu ha sido desactivada, contacta a tu administrador.",
			});
		} else {
			toaster.error({
				icon  : AlertTriangle,
				title : "Error",
				body  : "No se ha podido iniciar sesión, revisa tu conexión a internet.",
			});
		}
	})();

	return (
		<LoginPage
			delegations={{
				hookForm,
				handleLoginSubmit,
			}}
		/>
	);
};

const mapDispatchToProps = bindAll({ AuthService });

export default connect(null, mapDispatchToProps)(LoginPageContainer);
