import { Link } from "react-router-dom";
import { User } from "react-feather";

// // Own Components
import { Password }   from "Resources/icons";
import FormInputGroup from "Components/Global/FormInputGroup";
import AuthForm       from "Components/AuthForm";

const LoginPage = ({
	delegations : {
		hookForm,
		handleLoginSubmit,
	},
}) => {
	return (
		<div id="LoginPage">
			<AuthForm
				Footer={Footer}
				labelBtn="Iniciar Sesión"
				title="¡Bienvenido de Nuevo!"
				handleSumbitForm={handleLoginSubmit}
				subTitle="Ingresa tu usuario y contraseña para acceder al portal."
			>
				<FormInputGroup
					name="user"
					label="Nombre de Usuario"
					icon={<User  size={16} />}
					register={hookForm.register}
					errors={hookForm.formState.errors}
					placeholder="Ingresa el nombre de usuario"
				/>
				<FormInputGroup
					type="password"
					name="password"
					label="Contraseña"
					icon={<Password size={16} />}
					register={hookForm.register}
					errors={hookForm.formState.errors}
				/>
			</AuthForm>
		</div>
	);
};

const Footer = 	() => (
	<>
		<p>¿Olvidaste tu contraseña?</p>
		<Link to="/auth/recover_password">Restablécela Aquí.</Link>
	</>
);

export default LoginPage;
